import "./styles.css";
import React, { Component } from "react";

class Clock extends React.Component {
  formatName(user) {
    return user.firstName + " " + user.lastName;
  }
  state = {
    user: {
      firstName: "Test",
      lastName: ""
    },
    styles: {
      height: "200px"
    }
  };

  user = {
    firstName: "Test",
    lastName: ""
  };

  cd() {
    console.log("work");
    this.setState({ styles: { height: "300" } });
  }

  render() {
    return (
      <div className="App">
        <div class="element" style={this.styles} onClick={this.cd}>
          Hello {this.formatName(this.user)}
        </div>
      </div>
    );
  }
}

export default Clock;
